<template>
  <div class="xui-vue markdown-section">
    <div class="main">
      <h1>notification</h1>
      <blockquote>
        <p>: .</p>
      </blockquote>
      <h5 id="">:</h5>
      <codes :codes="params"></codes>
      <ul>
        <li>
          <code>title</code>
        </li>
        <li>
          <code>content</code>
        </li>
        <li>
          <code>duration</code>, 0, 3s
        </li>
        <li>
          <code>btn</code>, 
        </li>
        <li>
          <code>fn</code>
        </li>
        <li>
          ? :
          <code>xumeng0611@gmail.com</code>
        </li>
      </ul>
      <h3></h3>
      <p>1., </p>
      <xui-button type="default" @click="notification1"></xui-button>
      <codes :codes="notification_1"></codes>
      <p>2., </p>
      <xui-button type="default" @click="notification2"></xui-button>
      <codes :codes="notification_2"></codes>
    </div>
  </div>
</template>

<script type="text/javascript">
import Codes from "@/assets/codes.vue";

export default {
  name: "notification",
  data() {
    return {
      params: `this.$notification({
    title: string,
    content: string,
    duration: number,
    btn: string,
    fn: callback,
});
`,
      notification_1: `this.$notification({
    title: 'notification title',
    content: 'This is the content of the notification.',
});
`,
      notification_2: `this.$notification({
    title: 'notification title',
    content: 'This is the content of the notification.',
    duration: 5000,
    fn(){
        this.$message('notification closed');
    },
});
`,
    };
  },
  mounted() {
    if ("ontouchstart" in window) {
      let tar = document.getElementById("app");
      tar.querySelector(".test").classList.add("folded");
      tar.querySelector(".main").classList.add("folded");
    }
  },
  methods: {
    notification1() {
      this.$notification({
        title: "notification title",
        content: "This is the content of the notification.",
      });
    },
    notification2() {
      this.$notification({
        title: "notification title",
        content: "This is the content of the notification.",
        duration: 5000,
        btn: "confirm",
        fn() {
          this.$message("notification closed");
        },
      });
    },
  },
  components: {
    Codes,
  },
};
</script>

<style >
#test {
  height: 300px;
  position: relative;
  background: #ccc;
}
</style>